// 库存车页面
import Exhibition from './base/exhibition.vue'
import Kucunnav from './base/kucunnav.vue'
import List from './base/list.vue'
export default{
	data(){
		return{
			carId:0,
			brandId:0,
			page:1,
			count:1,
		}
	},
	// 下拉刷新
	onPullDownRefresh(){
		this.page = 1
		this.carId = 0,
		this.brandId = 0,
		this.$children[2].getGoodsList(this.page)
		setTimeout(()=>{
			uni.stopPullDownRefresh();
		}, 2000)
		
	},
	// 上拉加载
	onReachBottom(){
		if (this.page < this.count) {
			this.page ++;
		}else{
			return
		}
		this.$children[2].getGoodsList(this.page)
	},
	components:{
		Exhibition,
		Kucunnav,
		List
	},
	methods:{
		// 车辆类型筛选
		handleCarType(index){
			this.carId = parseInt(index);
		},
		// 品牌筛选
		handleBrands(brandId){
			this.brandId = parseInt(brandId);
		},
		// 刷新数据
		changePage(page,count){
			this.page = page;
			this.count = count;
		}
	}
}
